@import url(https://fonts.googleapis.com/css?family=Lora:400i|Montserrat:500,700|Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* FONTS, SIZE AND COLORS */
/* ---------------------------------------- */

.App {
  background-color: white;
  color: #242424;
  font-size: 1em;
  font-family: "Raleway", sans-serif;
}
.App p,
h2,
h3,
h4,
h5,
small,
hr,
span {
  margin: 5% 10% 0;
}

.App h1 {
  margin: 15% 20% 5% 10%;
  font-size: 1.5em;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
}

.App h2 {
  font-size: 1.3em;
  line-height: 1.3em;
  font-family: "Lora", serif;
}
.App h3 {
  font-size: 1.2em;
  line-height: 1.2em;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  /* text-transform: uppercase; */
  font-weight: 700;
}

.App h4 {
  font-size: 1em;
  font-family: "Lora", serif;
}

.App h5 {
  font-size: 1em;
  line-height: 1em;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: 700;
}

.App a {
  font-family: "Raleway", sans-serif;
  color: aliceblue;
  text-decoration: none;
  margin: 5%;
}

.App small {
  color: #242424;
  font-size: 0.8em;
  font-family: "Lora", serif;
  text-align: right;
}

footer {
  margin: 5vh 0 0 0;
  padding: 5%;
  text-align: center;
  background-color: #242424;
  color: aliceblue;
}

.date-day {
  color: aliceblue;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: bolder;
  font-size: 1.4em;
  text-decoration: underline;
}
.date-month {
  color: aliceblue;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: bolder;
  font-size: 1.4em;
}
.date-year {
  color: aliceblue;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: bolder;
  font-size: 1.4em;
  position: absolute;
  bottom: 10px;
}

.logo {
  width: 150px;
  padding-top: 5%;
}
/* SEPARATORS */

.App hr {
  border: 0.5px solid silver;
}

.App .small-hr {
  width: 50px;
  border: 2px solid blue;
  /* margin: 10px auto; */
}

/* SECTION OFF-GRID SETTINGS AND PADDING */

.App .off-grid-section {
  margin-bottom: -50px;
}

.App .Header {
  margin-bottom: 150px;
}

.padding-top {
  padding-top: 15%;
