/* HEADER FOR APP ONLY */

.Header {
  height: 30vh;
  background-image: -webkit-linear-gradient(
      rgba(255, 127, 80, 0.6) 0%,
      rgba(255, 127, 80, 0.6) 50%
    ),
    url("https://images.unsplash.com/photo-1508973379184-7517410fb0bc?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2850&q=80");
  background-image: linear-gradient(
      rgba(255, 127, 80, 0.6) 0%,
      rgba(255, 127, 80, 0.6) 50%
    ),
    url("https://images.unsplash.com/photo-1508973379184-7517410fb0bc?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2850&q=80");

  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  position: relative;
}

.header-text {
  background-color: white;
  border-radius: 5px;
  margin: 15vh 10% 10% 10%;
  padding-bottom: 50px;
  width: 80%;
  color: #242424;
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.1);
  position: absolute;
}

