/* BTN EFFECT */

.btn {
  position: relative;
  color: var(--inv);
  -webkit-transition: all 600ms cubic-bezier(0.77, 0, 0.175, 1);
  transition: all 600ms cubic-bezier(0.77, 0, 0.175, 1);
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn:before,
.btn:after {
  content: "";
  position: absolute;
  -webkit-transition: inherit;
  transition: inherit;
  z-index: -1;
}

.btn:hover {
  color: white;
  background-color: blue;
  -webkit-transition-delay: 0.6s;
          transition-delay: 0.6s;
}

.btn:hover:before {
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.btn:hover:after {
  background: blue;
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}

/* From Top */

.from-top:before,
.from-top:after {
  left: 0;
  height: 0;
  width: 100%;
}

.from-top:before {
  bottom: 0;
  border: 1px solid var(--inv);
  border-top: 0;
  border-bottom: 0;
}

.from-top:after {
  top: 0;
  height: 0;
}

.from-top:hover:before,
.from-top:hover:after {
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

